import {
  Box,
  Center,
  Heading,
  Link,
  Text,
  Container,
  FormLabel,
} from "@chakra-ui/react";
import { ExternalLinkIcon } from "@chakra-ui/icons";
import { useConnection, useWallet } from "@solana/wallet-adapter-react";
import { Image } from "@chakra-ui/image";
import { ToastContainer, toast } from "react-toastify";
import { useCandyMachine } from "../../hooks/useCandyMachine";
import { MintError } from "../../types";
import { getTransactionLink, mint, parseMintError } from "../../utils";
import {
  MaxButton,
  StyledButton,
  StyledInput,
  StyledWalletMultiButton,
} from "./styles";
import { Spinner } from "@chakra-ui/spinner";
import { ImageContainer } from "../HomePage/styles";
import { INVADERS_STORAGE_MINT_KEY } from "../../constants";
import { useState } from "react";

export default function MintPage() {
  const wallet = useWallet();
  const { connection } = useConnection();
  const { candyMachineState, setIsMinting, isMinting } = useCandyMachine();
  const [quantity, setQuantity] = useState(0);

  const handleClick = async () => {
    setIsMinting(true);

    for (var i = 0; i < quantity; i++) {
      let numMinted = 0;

      try {
        numMinted = Number(
          localStorage.getItem(INVADERS_STORAGE_MINT_KEY) || "0"
        );
      } catch (e) {}

      if (numMinted >= 10) {
        toast.error("Sorry! You have already minted the max of 10.");
        setIsMinting(false);
        break;
      }

      try {
        const tx = await mint({ wallet, connection });
        toast.success(
          <>
            Success!{" "}
            <Link href={getTransactionLink(tx as string)} isExternal>
              View transaction <ExternalLinkIcon />
            </Link>
          </>
        );
        localStorage.setItem(INVADERS_STORAGE_MINT_KEY, String(numMinted + 1));
      } catch (e) {
        const error = parseMintError(e as MintError);
        toast.error(error);
      }
    }

    setIsMinting(false);
  };

  const handleQuantityChange = (quantity: number) => {
    setQuantity(quantity);
  };

  return (
    <Container maxW="full" textAlign="center">
      <ToastContainer theme="dark" />
      <Heading
        as="h1"
        mt={10}
        mb={8}
        fontWeight={700}
        fontSize={{ base: 30, md: 40, lg: 60 }}
        textAlign="center"
      >
        Sol Invaders
      </Heading>
      <Container w={["md", "md", "xl"]} mb={12} display="flex" flexDir="column">
        <Box background="whiteAlpha.400" className="nes-container is-rounded">
          <Center>
            <ImageContainer className="nes-container is-rounded" maxW={250}>
              <Image
                src="images/samples/mystery.png"
                height={250}
                rounded={0}
              />
            </ImageContainer>
          </Center>
          <Text m={4}>! Welcome to Sol Invaders !</Text>
          <Text m={4}>Total Invaders Minted:</Text>
          {candyMachineState ? (
            <Text color="#79FE0C">
              {Math.max(candyMachineState?.itemsRedeemed.toNumber() - 1367, 0)}
              /3333
            </Text>
          ) : (
            <Spinner size="lg" color="#79FE0C" />
          )}
          <Box mt={4}>
            <StyledWalletMultiButton className="nes-btn" />
          </Box>
          {wallet?.connected && (
            <Box mt={4}>
              <Box className="nes-field" position="relative" mb={4} mx={8}>
                <MaxButton
                  size="xs"
                  top={4}
                  variant="solid"
                  colorScheme="purple"
                  right={4}
                  onClick={() => setQuantity(10)}
                >
                  Max
                </MaxButton>
                <FormLabel>Quantity</FormLabel>
                <StyledInput
                  _focus={{ outline: "none" }}
                  className="nes-input"
                  style={{ background: "transparent" }}
                  onChange={(e) => handleQuantityChange(Number(e.target.value))}
                  min={1}
                  max={10}
                  type="number"
                  placeholder="Enter Quantity"
                  value={quantity > 0 ? String(quantity) : ""}
                />
              </Box>
              <StyledButton
                className="nes-btn is-primary"
                onClick={() => handleClick()}
                css={{ pointerEvents: isMinting ? "none" : "auto" }}
                disabled={isMinting || quantity < 1 || quantity > 10}
              >
                {isMinting && <Spinner mr={2} size="sm" />}
                Mint
              </StyledButton>
              <Text color="white" fontSize={12} pt={2}>
                Minting costs 0.25 SOL
              </Text>
            </Box>
          )}
        </Box>
      </Container>
    </Container>
  );
}
